import React, { useEffect, useRef } from 'react';
import sprite from '../../images/icons.svg';
import Icon from 'utils/Icon/Icon';
import {
  ApproveBtn,
  Backdrop,
  BtnContainer,
  BtnText,
  CancelBtn,
  CloseIcon,
  Container,
  Content,
  TrashIcon,
} from './ModalApproveAction.styled';

const Modal = ({ isOpen, onRequestClose, onApprove, children }) => {
  const modalRef = useRef();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const handleKeyDown = event => {
    if (event.key === 'Escape') {
      onRequestClose();
    }
  };

  const handleBackdropClick = event => {
    if (modalRef.current === event.target) {
      onRequestClose();
    }
  };

  useEffect(() => {
    if (isOpen) {
      document.addEventListener('keydown', handleKeyDown);
    } else {
      document.removeEventListener('keydown', handleKeyDown);
    }

    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [handleKeyDown, isOpen]);

  return isOpen ? (
    <Backdrop ref={modalRef} onClick={handleBackdropClick}>
      <Container>
        <CloseIcon onClick={() => onRequestClose()}>
          <Icon name="cross" size="24" color="var(--dark-blue)" />
        </CloseIcon>

        <Content>{children}</Content>
        <BtnContainer>
          <CancelBtn onClick={() => onRequestClose()}>Cancel</CancelBtn>
          <ApproveBtn onClick={onApprove}>
            <BtnText>Yes</BtnText>
            <TrashIcon width={24} height={24}>
              <use href={`${sprite}#icon-trash-2`}></use>
            </TrashIcon>
          </ApproveBtn>
        </BtnContainer>
      </Container>
    </Backdrop>
  ) : null;
};

export default Modal;
